body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__time {
  transform-origin: bottom;
  animation-name: watch;
  animation-iteration-count: infinite;
  position: absolute;
  bottom: 50%;
}

.stopwatch__time--minutes {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation-duration: 3600s;
  animation-timing-function: steps(60, end);
}

.stopwatch__time--seconds {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation-duration: 60s;
  animation-timing-function: linear;
}

.stopwatch__center {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
}

.stopwatch--speed-up .stopwatch__time--seconds {
  animation-duration: 10s;
}

.stopwatch--speed-up .stopwatch__time--minutes {
  animation-duration: 600s;
}

@keyframes watch {
  0% {
    transform: rotateZ(0);
  }

  100% {
    transform: rotateZ(360deg);
  }
}
